<template>
  <div class="recordwarp" :style="{height: warpHeight + 'px'}">
    <!-- <HEADER></HEADER> -->
    <div class="recordcon" v-show="JSON.stringify(recordlist) !== '{}'">
      <h3>【 购买记录 】</h3>
      <ul class="recordlist">
        <li v-for="item in recordlist" :key="item.coupons" :class="item.status === 2?fontGrey:''">
          <div class="textcon">
            <p class="resStatus">卡券编号：{{item.coupon}}
            </p>
            <p class="resStatus">卡券礼包：{{item.productName}} X{{item.num}}
            </p>
            <p>购买日期：{{ dateForm(item.orderTime) }}</p>
          </div>
          <div class="btncon">
            <mt-button v-show="item.status !== 2" size="small" type="primary" plain
              @click="onBtn(item.coupon)"
              >前往兑换</mt-button>
            <span v-show="item.status === 2">{{item.statusText}}</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="nonecon" v-show="JSON.stringify(recordlist) == '{}'">
      <!-- <img src="../../assets/none.png" alt="" width="50%"> -->
      <p>当前没有购买记录</p>
    </div>
  </div>
</template>

<style lang="less">
.recordwarp {
  height: 100%;
  background: rgb(255, 4, 4);
}
.recordcon {
  height: 90%;
  width: 90%;
  padding: 5%;
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
}
.recordlist {
  height: auto;
  overflow-y: scroll;
  padding: 0 10px;
  margin: 0;
}
.recordlist li {
  list-style-type: none;
  margin-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid gainsboro;
  display: flex;
  flex: 1;
  justify-content: space-between;
  .btncon {
    display: flex;
    align-items: center;
    button {
      border-radius: 8px;
    }
  }
}
.recordlist .resStatus {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.recordcon h3 {
  text-align: left;
  padding-top: 20px;
}
.recordcon p {
  text-align: left;
  margin: 10px 0;
  font-size: 0.8em;
}
.fontGrey {
  color: gainsboro;
}
.recordwarp .nonecon {
  width: 100%;
  // height: 80%;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.recordwarp .nonecon p {
  color: rgb(180, 180, 180);
}
/* covered mint */
.recordWarp .mint-button--small {
  padding: 0 5px;
  margin-left: 2px;
  height: 30px;
}
/* covered end */
</style>

<script>
import HEADER from '@/components/header.vue'
// YBB.hybrid.navigation.hide();

export default {
  name: 'record',
  components: { HEADER },
  data () {
    return {
      warpHeight: null,
      bodyHeight: null,
      recordlist: this.$store.state.recordlist,
      fontGrey: 'fontGrey'
    }
  },
  methods: {
    dateForm(time) {
      let date = time;
      return date ? date.substring(0, 10) : '';
    },
    onBtn (text) {
      this.$toast({ message: '复制成功', position: 'top' });
      this.$indicator.open('跳转中...')
      setTimeout(() => {
        this.$indicator.close()
        // window.location.href = coupons
        this.$router.push(
          {
            name: 'consume',
            params: {path: this.$router.currentRoute.path, orderNo: text}
          }
        )
      }, 1000);
    },
  },
  mounted () {
    this.warpHeight = this.bodyHeight = window.screen.height - 40
    YBB.hybrid.navigation.setTitle({title: '购买记录'})
  }
}
</script>



